import { useState } from '@wordpress/element';

const SearchFilter = ({ docs }) => {
    const [searchTerm, setSearchTerm] = useState("");
    const [data, setData] = useState([
        { id: 1, name: "John Doe" },
        { id: 2, name: "Jane Doe" },
        { id: 3, name: "Jim Brown" }
    ]);

    const handleChange = event => {
        setSearchTerm(event.target.value);
    };

    const filteredData = data.filter(item =>
        item.name.toLowerCase().includes(searchTerm.toLowerCase())
    );

    return (
        <div className="relative mt-5 rounded-md shadow-sm">
            <input type="text" placeholder="Type here" value={searchTerm} onChange={handleChange}
                className="w-80 h-10 text-sm focus:!border-indigo-300 !pl-3 !rounded-md !border-gray-300" />
            <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center pr-3">
                <svg className="h-5 w-5 text-red-500" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
                    <path fillRule="evenodd" clipRule="evenodd"
                        d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-8-5a.75.75 0 01.75.75v4.5a.75.75 0 01-1.5 0v-4.5A.75.75 0 0110 5zm0 10a1 1 0 100-2 1 1 0 000 2z" />
                </svg>
            </div>
        </div>
    );
}

export default SearchFilter;
